*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'monsteratt', sans-serif;
}

.app{
  background-image: url('./assets/one.jpg');
  background-attachment: fixed;
  background-position: center; 
  background-size: cover;
  
  transition: 0.4s ease-out;
}

.app.warm{
  background-image: url(./assets/two.jpg);
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  margin: 0 auto;
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 13px 13px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}

.location-box .location{
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date{
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #FFF;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather{
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.credit {
  margin-top: 100px;
  text-align: center;
  color: #FFF;
  text-shadow: rgba(50, 50, 70, 0.5);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.credit a:link, a:visited {
  background-color: #2e52a0;
  color: white;
  padding: 14px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 15px;
}

.credit a:hover, a:active {
  background-color: rgb(102, 149, 221);
}